
































































































































































































































































#mobileUserManagement {
  position: relative;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
}
